/* SKW Styles */

.skw-list:hover .skw-content:after {
  background: rgba(0,0,0,.6);
}

.skw-itemActive .skw-content:after {
  background: rgba(0,0,0,0);
  -webkit-transition-delay: all .3s;
  -webkit-transition: all .8s;
}

.skw-visible .skw-content :after {
  background: rgba(0,0,0,0)!important;
}

.skw-list .skw-content:hover:after {
  background: rgba(0,0,0,0);
  -webkit-transition: all 1.2s;
  transition: all 1.2s;
  box-shadow: 0;
}

.skw-content:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.6);
  -webkit-transition: all .9s;
  transition: all .9s;
  top: 0;
  left: 0;
  pointer-events: none;
}

.skw-list > li:after {
  content: "";
  position: absolute;
  top: -10%;
  left: 0;
  width: 100%;
  height: 120%;
  z-index: 9;
  -webkit-transition: all 1s;
  pointer-events: none;
}

.skw-nav:hover {
  opacity: 1;
  box-shadow: 0 0 4px rgba(0,0,0,.4);
  background: rgba(0,0,0, .7);
}

.skw-caption {
  -webkit-transition: all .8s;
  -moz-transition: all .8s;
  -ms-transition: all .8s;
  transition: all .8s;
  z-index: 999;
  a {

    @extend .btn-brand-default;
    @extend .btn-dark;
    font-family: $skw-font;
    font-weight: bold;
    font-size: 3vw;
    letter-spacing:$font-button-letter-spacing;
    background-color: rgba(0, 0, 0, 0.5);
    border: $skw-border-width solid #fff;
    padding: $skw-buttons-padding;
    margin: 0 auto;
    padding-top: $skw-text-padding;

    &:hover {
      background: $color-brand-primary;
      border-color: $color-brand-primary;
      color: #fff;
    }

    @media screen and (max-width: 767px) {
      font-size:30px;
    }
  }
}

.skw-itemNext .skw-content:after {
  background-color: $skw-bg-next;
}

.skw-itemPrev .skw-content:after {
  background-color: $skw-bg-prev;
}

.main-slide .skw-content {
  padding-top: 20px;
}
.skewSlider:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  box-shadow: inset 0 0 2px rgba(0,0,0,0.2);
  pointer-events: none;
}

.main-slide .skw-list > li .skw-content.slide1 {
  -webkit-transition: all 1.2s;
  transition: all 1.2s;
}

.skw-caption {
  color: #fff;
  -webkit-transition: all .8s;
  -moz-transition: all .8s;
  -ms-transition: all .8s;
  transition: all .8s;
  z-index: 999;
}

.main-slide .skw-list > li .skw-content.slide2 {
  background-color: #9ACD32;
  -webkit-transition: all 1.2s;
  transition: all 1.2s;
}
.main-slide .skw-list > li.skw-itemActive .skw-content.slide2 {
  background-color: #4169E1;
}

.info h2 {
  color: #fff;
  font-weight: 700;
  font-style: italic;
  font-size: 2.5em;
  padding-left: 20%;
  padding-top: 30px;
}

.seef {
  text-align: center;

  span {
    font-size: 3em;
    cursor: pointer;
    color: #FF0033;
    -webkit-transition: .5s all;
    transition: .5s all;
    display: inline-block;
    -webkit-transform: scale(1) translateY(0);
    transform: scale(1) translateY(0);
    &:hover{
      -webkit-transform: scale(1.3) translateY(6px);
      transform: scale(1.3) translateY(6px);
    }
  }
  h3{
    font-style: italic;
    font-size: 3.1em;
    color: #333;
    margin-bottom: 0;

  }
}

.features {
  margin-top: 50px;
  padding: 0 10%;
}

.feature {
  display: inline-block;
  width: 32%;
  padding: 20px;
  vertical-align: text-top;

  .feature h4 {
    font-size: 1.2em;
  }

}

.demos {
  margin-top: 50px;
  padding: 0 12%;
  h2 {
    text-align: center;
  }
  .demos h4 {
    color: #333;
  }

  .demo {
    text-align: center;
    width: 45%;
    display: inline-block;
    margin: 2.3%;
    img {
      box-shadow: 0 0 5px rgba(0,0,0,.3);
    }
  }
}

.responsive {
  margin-top: 50px;
  padding: 0 12% 50px 12%;
  text-align: center;

  p {
    margin: 50px 10% 20px 10%;
  }
  .get {
    margin-top: 20px;
    padding: 20px 30px;
    font-size: 1.5em;
    float: none;
    display: inline-block;
  }
}


.info .get {
  padding: 20px 30px;
  font-size: 1.5em;
  float: none;
  display: inline-block;
}

.demo-title {
  padding: 50px 15%;
  h2 {
    float: left;
    margin: 0;
    padding: 0;
    font-size: 3.1em;
    .get {
      float: right;
    }
  }
}

.slide2 { text-align: center; }
.slide2 img { max-width: 55%; margin-top: 50px; }


.slide4 { text-align: center; }
.slide4 img { max-width: 50%; margin-top: 50px; }

.light .demo-title h2 {
  color: #333;
}

.skewSlider li { display: none; }

.cont { margin-bottom: 50px; }

@media only screen and (max-width: 1024px) {


  .skewSlider { margin-top: 0; }

}

@media only screen and (max-width: 850px) {
  .feature {
    width: 49%;
  }
  .demos .demo {
    margin: 2%;
  }
}

@media only screen and (max-width: 780px) {
  .main-slide .slide1 h2:before {
    -webkit-transform: skew(-20deg,0);
    transform: skew(-20deg,0);
  }

  .main-slide .slide1 h2 {
    font-size: 1.8em;
    padding: 15px 30px;
  }
}

@media only screen and (max-width: 600px) {
  .main-slide .slide1 h2:before {
    -webkit-transform: skew(-20deg,0);
    transform: skew(-20deg,0);
  }
  .main-slide .slide1 h2 {
    font-size: 1.4em;
    padding: 10px 20px;
  }
  .main-slide .slide1 .copy {
    position: absolute;
    bottom: 10%;
    right: 20%;
  }
  .skw-nav {
    display: none;
  }

  .demos .demo {
    margin: 2%;
    width:  96%;
  }
  .feature {
    width: 98%;
  }
  .skw-caption {
    color: #fff;
    -webkit-transition: all .8s;
    -moz-transition: all .8s;
    -ms-transition: all .8s;
    transition: all .8s;
    z-index: 999;
    opacity: 0;
  }
}