.newsletter-stats {
  background-image: url(#{$newsletter-bg-img});
  background-size: cover;
  background-position: center;
  .home-newsletter {
    text-align: center;
    padding: 30px 15px 50px 15px;

    form {
      input {
        font-family: $font-newsletter-input;
        background: transparent;
        border: 2px solid $color-newsletter-input-border;
        padding: 10px 20px;
        outline: none;
        font-size: 16px;
        color: $color-brand-primary;
        -webkit-appearance: none;
        border-radius: 0;
      }
      #submit {
        width: 100px;
        color: $button-brand-primary-text;
        background: $color-newsletter-button-bg;
        border: 2px solid $color-newsletter-button-bg;
        -webkit-transition: all .3s ease-out 0.1s;
        -moz-transition: all .3s ease-out 0.1s;
        -o-transition: all .3s ease-out 0.1s;
        transition: all .3s ease-out 0.1s;
        &:hover {
          border: 2px solid $color-brand-primary;
          background: $btn-light-bg;
          color: $color-brand-primary;
        }
      }
      ::placeholder {

        color:$placeholder-text-color;
      }
    }
  }
  .stats-row {
    margin: 0;
    padding: 25px;
    background: $color-stats-bg;
    text-align: center;
    .stats-div {
      display: inline-block;
      border-right: 1px solid #aaa;
      padding: 5px 40px;
    }
    .stats-div:nth-child(4) {
      border-right: none;
    }
    p {
      font-family: $font-newsletter-input;
      color: $color-stats-title-value;
      font-size: 24px;
      margin: 0 0 15px 0;
    }
    span {
      font-family: $font-countdown-title;
      color: $color-stats-value;
      font-size: 40px;
    }
    .odometer.odometer-auto-theme, .odometer.odometer-theme-default {
      line-height: 34px;
    }
  }
}

.input-effect {
  position: relative;
  display: inline-block;
}
.effect-9 ~ .focus-border:before,
.effect-9 ~ .focus-border:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 2px;
  background-color: $color-brand-primary;
  transition: 1s;
  transition-delay: 0.4s;
}
.effect-9 ~ .focus-border:after {
  top: auto;
  bottom: 0;
  right: auto;
  left: 0;
  transition-delay: 0.8s;
}
.effect-9 ~ .focus-border i:before,
.effect-9 ~ .focus-border i:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 2px;
  height: 0;
  background-color: $color-brand-primary;
  transition: 0.4s;
}
.effect-9 ~ .focus-border i:after {
  left: auto;
  right: 0;
  top: auto;
  bottom: 0;
  transition-delay: 0.6s;
}
.effect-9:focus ~ .focus-border:before,
.effect-9:focus ~ .focus-border:after {
  width: 100%;
  transition: 0.4s;
  transition-delay: 0.8s;
}
.effect-9:focus ~ .focus-border:after {
  transition-delay: 0.4s;
}
.effect-9:focus ~ .focus-border i:before,
.effect-9:focus ~ .focus-border i:after {
  height: 100%;
  transition: 0.4s;
}
.effect-9:focus ~ .focus-border i:after {
  transition-delay: 0.6s;
}


@media screen and (max-width: 767px) {
  .newsletter-stats {
    .home-newsletter {
      padding: 10px 15px 15px 15px;
      p:first-child {
        font-size: 30px;
        letter-spacing: 0;
      }
      p:nth-child(2) {
        margin: 5px 0 15px 0;
      }
      form input {
        width: 100%;
        margin-bottom: 0;
      }
      form #submit {
        width: 100%;
        margin-bottom: 0;
      }
      .input-effect {
        display: block;
        margin-bottom: 10px;
      }
    }
    .stats-row {
      padding: 15px 0;
      padding-bottom: 0;
      .stats-div {
        padding: 5px 10px;
        margin-bottom: 15px;
      }
    }
  }

}


.game-countdown {
  background: $color-brand-primary;
  padding: 15px 0 25px 0;
  text-align: center;
  color: #fff;
  .game-title {
    font-family: $font-countdown-number;
    font-size: 30px;
    text-transform: uppercase;
    margin: 0
  }
  #clock {
    margin-top: 20px;
    margin-bottom: 20px;
    color: #fff;
    div {
      display: inline-block;
      font-family: $font-countdown-number;
      font-size: 60px;
      line-height: 35px;
      margin: 0 5px;
      span {
        font-family: $font-countdown-number;
        font-size: 16px;
        display: block
      }
    }
  }
  a {
    @extend .btn-brand-default, .btn-light, .btn-trans ;
    margin-bottom:0;

    &:hover {
      @extend .btn-light;
    }
  }
}
